import React, { useState, useEffect } from 'react';
import CircularIndeterminate from './components/common/Progress';
import NavBar from './components/NavBar';
const getTypeColor = (type) => {
  const normal = '#F5F5F5';
  const typeColors = {
    normal,
    fire: '#FDDFDF',
    grass: '#DEFDE0',
    electric: '#FCF7DE',
    ice: '#DEF3FD',
    water: '#DEF3FD',
    ground: '#F4E7DA',
    rock: '#D5D5D4',
    fairy: '#FCEAFF',
    poison: '#98D7A5',
    bug: '#F8D5A3',
    ghost: '#CAC0F7',
    dragon: '#97B3E6',
    psychic: '#EAEDA1',
    fighting: '#E6E0D4',
  };
  return typeColors[type] || normal;
};

const App = () => {
  const [pokemonData, setPokemonData] = useState([]);
  const [visibleCount, setVisibleCount] = useState(20);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchPokemon = async () => {
      const promises = [];
      for (let i = 1; i <= 300; i++) {
        const url = `https://pokeapi.co/api/v2/pokemon/${i}`;
        promises.push(fetch(url).then((res) => res.json()));
      }
      try {
        const results = await Promise.all(promises);
        const pokemon = results.map((result) => ({
          name: result.name,
          image: result.sprites['front_default'],
          type: result.types.map((type) => type.type.name).join(', '),
          weight: result.weight,
          height: result.height,
          id: result.id,
        }));
        setPokemonData(pokemon);
        setLoading(false);
      } catch (error) {
        setError(error);
        setLoading(false);
      }
    };
    fetchPokemon();
  }, []);

  const handleSeeMore = () => {
    setVisibleCount(visibleCount + 20);
  };

  if (loading) {
    return <CircularIndeterminate />;
  }

  if (error) {
    return <div>Error: {error.message}</div>;
  }

  return (
    <div>
      <h1>Pokemon</h1>
      <NavBar />
      <ul style={{ listStyle: 'none', padding: 0 }}>
        {pokemonData.slice(0, visibleCount).map((pokeman) => (
          <li
            key={pokeman.id}
            className="card"
            style={{
              width: '15rem',
              padding: '10px',
              margin: '20px',
              backgroundColor: getTypeColor(pokeman.type),
            }}
          >
            <img className="card-image" src={pokeman.image} alt={pokeman.name} />
            <h2 className="card-title">
              {pokeman.id}. {pokeman.name}
            </h2>
            <p className="card-subtitle">Type: {pokeman.type}</p>
            <p className="card-subtitle">Height: {pokeman.height}</p>
            <p className="card-subtitle">Weight: {pokeman.weight}</p>
          </li>
        ))}
      </ul>
      {visibleCount < pokemonData.length && (
        <button onClick={handleSeeMore}>See More</button>
      )}
    </div>
  );
};

export default App;
